import axios from 'axios';
// 环境的切换
// if (process.env.NODE_ENV == 'development') {    
//     axios.defaults.baseURL = 'https://api.shanxilx.cn/index.php/apiv2/';} 
// else if (process.env.NODE_ENV == 'debug') {    
//     axios.defaults.baseURL = 'https://api.shanxilx.cn/index.php/apiv2/';
// } else if (process.env.NODE_ENV == 'production') {    
//     axios.defaults.baseURL = 'https://api.shanxilx.cn/index.php/apiv2/';
// }
axios.defaults.timeout = 10000;
axios.defaults.headers['Content-Type'] = 'application/json';
axios.interceptors.request.use(
	config => {
		config.data = config.data || {}
		config.data['cid'] = '1';
		return config;
	},err => {
			return Promise.reject(err);
	});
axios.interceptors.response.use(function (response) {
    return response;
  }, function (error) {
    return Promise.reject(error);
  });

export const getGoodsList = params => { return axios.post('/api/goods/index2', params).then(res => res.data)};
export const getBanner = params => { return axios.post('/api/goods/banner2', params).then(res => res.data)};


